/**
 * Servio para contar pginas de arquivos PDF
 * Implementa numerao correta: cada pgina do PDF = uma folha separada no processo
 */
class PDFPagesService {

  /**
   * Contar o nmero de pginas de um arquivo PDF
   * @param {File} arquivo - Arquivo PDF
   * @returns {Promise<number>} Nmero de pginas
   */
  static async contarPaginas(arquivo) {
    return new Promise((resolve, reject) => {
      try {
        const reader = new FileReader()

        reader.onload = function() {
          try {
            const arrayBuffer = this.result
            const uint8Array = new Uint8Array(arrayBuffer)

            // Converter para string para buscar padres
            let pdfText = ''
            for (let i = 0; i < uint8Array.length; i++) {
              pdfText += String.fromCharCode(uint8Array[i])
            }

            console.log(` Analisando PDF: ${arquivo.name}`)

            // Mtodo 1: Buscar objetos de pgina (/Type /Page)
            const pageRegex1 = /\/Type\s*\/Page[^s]/g
            const pages1 = pdfText.match(pageRegex1) || []

            // Mtodo 2: Buscar referncias de pgina
            const pageRegex2 = /\/Count\s+(\d+)/g
            const countMatches = pdfText.match(pageRegex2) || []
            let pages2 = 0
            if (countMatches.length > 0) {
              // Pegar o maior valor encontrado (normalmente  o total de pginas)
              pages2 = Math.max(...countMatches.map(match => {
                const num = match.match(/(\d+)/)
                return num ? parseInt(num[1]) : 0
              }))
            }

            // Mtodo 3: Buscar /Kids array (estrutura de pginas)
            const kidsRegex = /\/Kids\s*\[([^\]]*)\]/g
            let pages3 = 0
            let match
            while ((match = kidsRegex.exec(pdfText)) !== null) {
              const kidsContent = match[1]
              // Contar referncias de objetos (formato "n 0 R")
              const objRefs = (kidsContent.match(/\d+\s+0\s+R/g) || []).length
              if (objRefs > pages3) {
                pages3 = objRefs
              }
            }

            // Mtodo 4: Buscar marcadores especficos do PDF
            const pageKeywords = [
              /\/Page\s/g,
              /endobj[\s\S]*?\/Type\s*\/Page/g
            ]

            let pages4 = 0
            pageKeywords.forEach(regex => {
              const matches = pdfText.match(regex) || []
              if (matches.length > pages4) {
                pages4 = matches.length
              }
            })

            console.log(` Mtodos de contagem para ${arquivo.name}:`)
            console.log(`   Mtodo 1 (/Type /Page): ${pages1.length}`)
            console.log(`   Mtodo 2 (/Count): ${pages2}`)
            console.log(`   Mtodo 3 (/Kids): ${pages3}`)
            console.log(`   Mtodo 4 (Keywords): ${pages4}`)

            // Escolher o resultado mais provvel
            const resultados = [pages1.length, pages2, pages3, pages4].filter(n => n > 0)

            if (resultados.length === 0) {
              console.warn(` No foi possvel determinar o nmero de pginas para ${arquivo.name}. Assumindo 1 pgina.`)
              resolve(1)
              return
            }

            // Usar o valor mais comum, ou o maior se no houver consenso
            const contador = {}
            resultados.forEach(num => {
              contador[num] = (contador[num] || 0) + 1
            })

            let paginasFinais = Math.max(...resultados)

            // Se mltiplos mtodos concordam, usar esse valor
            const maisComum = Object.keys(contador).reduce((a, b) =>
              contador[a] > contador[b] ? a : b
            )

            if (contador[maisComum] >= 2) {
              paginasFinais = parseInt(maisComum)
            }

            console.log(` PDF ${arquivo.name}: ${paginasFinais} pginas detectadas`)

            // Validao: PDFs com mais de 500 pginas so suspeitos
            if (paginasFinais > 500) {
              console.warn(` PDF com ${paginasFinais} pginas detectadas - pode ser falso positivo`)
              paginasFinais = Math.min(paginasFinais, 50) // Limitar a 50 pginas por segurana
            }

            // PDFs devem ter pelo menos 1 pgina
            resolve(Math.max(1, paginasFinais))

          } catch (error) {
            console.error(` Erro ao analisar PDF ${arquivo.name}:`, error)
            console.warn(` Assumindo 1 pgina para ${arquivo.name} devido ao erro`)
            resolve(1) // Fallback seguro
          }
        }

        reader.onerror = function(error) {
          console.error(` Erro ao ler arquivo ${arquivo.name}:`, error)
          console.warn(` Assumindo 1 pgina para ${arquivo.name} devido ao erro de leitura`)
          resolve(1) // Fallback seguro
        }

        reader.readAsArrayBuffer(arquivo)

      } catch (error) {
        console.error(` Erro geral ao processar PDF ${arquivo.name}:`, error)
        console.warn(` Assumindo 1 pgina para ${arquivo.name}`)
        resolve(1) // Fallback seguro
      }
    })
  }

  /**
   * Processar arquivo PDF para numerao de pginas individuais
   * @param {File} arquivo - Arquivo PDF
   * @param {string} processoId - ID do processo
   * @param {string} tipoDocumento - Tipo do documento
   * @returns {Promise<Array>} Array com informaes de cada pgina
   */
  static async processarPDFParaPaginas(arquivo, processoId, tipoDocumento) {
    try {
      console.log(` Processando PDF para pginas individuais: ${arquivo.name}`)

      // Contar pginas do PDF
      const numeroPaginas = await this.contarPaginas(arquivo)

      console.log(` PDF ${arquivo.name} tem ${numeroPaginas} pgina${numeroPaginas > 1 ? 's' : ''}`)

      // Importar o servio de numerao
      const { default: NumeracaoDocumentosService } = await import('./numeracaoDocumentosService')

      // Separar as pginas do PDF em arquivos individuais
      const paginasIndividuais = await this.separarPaginasPDF(arquivo)

      // Obter prximos nmeros sequenciais para todas as pginas
      const numeracoes = []
      for (let pagina = 1; pagina <= numeroPaginas; pagina++) {
        const { numero, folha } = await NumeracaoDocumentosService.obterProximoNumero(processoId)
        numeracoes.push({
          numero,
          folha,
          pagina,
          nomeDocumento: numeroPaginas > 1 ?
            `${arquivo.name} (Pgina ${pagina}/${numeroPaginas})` :
            arquivo.name,
          arquivoPagina: paginasIndividuais[pagina - 1] // PDF individual da pgina
        })

        console.log(` Pgina ${pagina}: ${folha} - ${numeracoes[pagina-1].nomeDocumento}`)
      }

      return {
        numeroPaginas,
        numeracoes,
        nomeOriginal: arquivo.name
      }

    } catch (error) {
      console.error(` Erro ao processar PDF para pginas:`, error)
      throw new Error(`Falha ao processar PDF ${arquivo.name}: ${error.message}`)
    }
  }

  /**
   * Separar pginas do PDF em arquivos individuais
   * @param {File} arquivo - Arquivo PDF original
   * @returns {Promise<Array<Object>>} Array com informaes de cada pgina
   */
  static async separarPaginasPDF(arquivo) {
    try {
      console.log(` Preparando pginas do PDF: ${arquivo.name}`)

      const arrayBuffer = await arquivo.arrayBuffer()
      const numeroPaginas = await this.contarPaginas(arquivo)
      const paginasIndividuais = []

      for (let i = 0; i < numeroPaginas; i++) {
        //  SOLUO INTELIGENTE: Criar arquivo com marcador de pgina
        // O visualizador poder usar parmetros URL para mostrar a pgina especfica
        const nomeArquivoPagina = `${arquivo.name.replace('.pdf', '')}_p${i + 1}.pdf`

        // Criar um objeto File com metadados da pgina
        const arquivoPagina = new File([arrayBuffer], nomeArquivoPagina, {
          type: 'application/pdf',
          lastModified: Date.now()
        })

        // Adicionar propriedades customizadas para identificar a pgina
        arquivoPagina.paginaNumero = i + 1
        arquivoPagina.totalPaginas = numeroPaginas
        arquivoPagina.arquivoOriginal = arquivo.name

        paginasIndividuais.push(arquivoPagina)
      }

      console.log(` ${numeroPaginas} pginas preparadas (arquivo original preservado com identificadores)`)
      return paginasIndividuais

    } catch (error) {
      console.error(` Erro ao preparar pginas do PDF:`, error)
      throw error
    }
  }
}

export default PDFPagesService