// Usar PDF.js via CDN para melhor compatibilidade
let pdfjsLib = null
let loadingPromise = null

// Carregar PDF.js dinamicamente
const loadPDFJS = async () => {
  // Evitar mltiplos carregamentos simultneos
  if (loadingPromise) {
    return await loadingPromise
  }

  if (pdfjsLib) {
    return pdfjsLib
  }

  loadingPromise = new Promise(async (resolve, reject) => {
    try {
      console.log(' Iniciando carregamento do PDF.js...')

      if (typeof window === 'undefined') {
        throw new Error('PDF.js s funciona no navegador')
      }

      // Verificar se PDF.js j est carregado
      if (window.pdfjsLib) {
        console.log(' PDF.js j estava carregado')
        pdfjsLib = window.pdfjsLib
        resolve(pdfjsLib)
        return
      }

      // Carregar PDF.js via CDN
      console.log(' Carregando PDF.js do CDN...')
      const script = document.createElement('script')
      script.src = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/3.11.174/pdf.min.js'

      script.onload = () => {
        try {
          console.log(' Script PDF.js carregado')

          // Aguardar um pouco para garantir que a biblioteca esteja disponvel
          setTimeout(() => {
            if (window.pdfjsLib) {
              pdfjsLib = window.pdfjsLib

              // Configurar worker
              console.log(' Configurando worker PDF.js...')
              pdfjsLib.GlobalWorkerOptions.workerSrc = 'https://cdnjs.cloudflare.com/ajax/libs/pdf.js/3.11.174/pdf.worker.min.js'

              console.log(' PDF.js carregado e configurado com sucesso')
              resolve(pdfjsLib)
            } else {
              reject(new Error('PDF.js no est disponvel aps carregamento'))
            }
          }, 100)
        } catch (error) {
          console.error(' Erro ao configurar PDF.js:', error)
          reject(error)
        }
      }

      script.onerror = (error) => {
        console.error(' Erro ao carregar script PDF.js:', error)
        reject(new Error('Falha ao carregar PDF.js do CDN'))
      }

      document.head.appendChild(script)

    } catch (error) {
      console.error(' Erro no carregamento do PDF.js:', error)
      reject(error)
    }
  })

  return await loadingPromise
}

class PDFRenderService {
  /**
   * Testa se o PDF.js est funcionando corretamente
   * @returns {Promise<boolean>} - True se PDF.js estiver funcionando
   */
  async testPDFJS() {
    try {
      console.log(' Testando PDF.js...')
      const pdfjs = await loadPDFJS()

      if (!pdfjs) {
        throw new Error('PDF.js no foi carregado')
      }

      if (!pdfjs.getDocument) {
        throw new Error('PDF.js no tem o mtodo getDocument')
      }

      console.log(' PDF.js est funcionando corretamente')
      console.log(' Verso PDF.js:', pdfjs.version || 'desconhecida')
      return true

    } catch (error) {
      console.error(' Erro no teste do PDF.js:', error)
      return false
    }
  }

  /**
   * Renderiza uma pgina especfica de um PDF como imagem base64
   * @param {string} pdfUrl - URL do PDF
   * @param {number} pageNumber - Nmero da pgina (1-based)
   * @param {number} scale - Escala de renderizao (padro: 1.5)
   * @returns {Promise<string>} - Imagem em base64
   */
  async renderPageAsImage(pdfUrl, pageNumber = 1, scale = 1.5) {
    try {
      console.log(' Carregando PDF:', pdfUrl, 'Pgina:', pageNumber)

      // Carregar PDF.js primeiro
      const pdfjs = await loadPDFJS()
      console.log(' PDF.js carregado com sucesso')

      // Verificar se a URL  vlida
      if (!pdfUrl || typeof pdfUrl !== 'string') {
        throw new Error('URL do PDF invlida: ' + pdfUrl)
      }

      // Carregar o documento PDF com timeout
      const loadingTask = pdfjs.getDocument({
        url: pdfUrl,
        verbosity: 0 // Reduzir logs do PDF.js
      })

      // Adicionar timeout de 30 segundos
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('Timeout ao carregar PDF')), 30000)
      })

      const pdf = await Promise.race([loadingTask.promise, timeoutPromise])

      console.log(' PDF carregado. Total de pginas:', pdf.numPages)

      // Verificar se a pgina existe
      if (pageNumber > pdf.numPages || pageNumber < 1) {
        throw new Error(`Pgina ${pageNumber} no existe. PDF tem ${pdf.numPages} pginas.`)
      }

      // Obter a pgina especfica
      const page = await pdf.getPage(pageNumber)

      // Obter viewport da pgina
      const viewport = page.getViewport({ scale })

      // Criar canvas para renderizao
      const canvas = document.createElement('canvas')
      const context = canvas.getContext('2d')

      // Configurar dimenses do canvas
      canvas.width = viewport.width
      canvas.height = viewport.height

      // Configurar fundo branco
      context.fillStyle = 'white'
      context.fillRect(0, 0, canvas.width, canvas.height)

      // Renderizar a pgina no canvas
      const renderContext = {
        canvasContext: context,
        viewport: viewport,
        background: 'white'
      }

      await page.render(renderContext).promise

      // Converter canvas para base64
      const imageData = canvas.toDataURL('image/png', 1.0)

      console.log(' Pgina renderizada com sucesso:', pageNumber)

      return imageData

    } catch (error) {
      console.error(' Erro ao renderizar PDF:', error)
      console.error('URL tentada:', pdfUrl)
      console.error('Pgina solicitada:', pageNumber)

      // Criar uma mensagem de erro mais especfica
      let errorMessage = 'Erro desconhecido ao renderizar PDF'

      if (error.message.includes('Timeout')) {
        errorMessage = 'Timeout ao carregar PDF. Verifique a conexo.'
      } else if (error.message.includes('URL')) {
        errorMessage = 'URL do PDF invlida ou inacessvel'
      } else if (error.message.includes('Failed to fetch')) {
        errorMessage = 'Falha ao buscar o PDF. Verifique se o arquivo existe.'
      } else if (error.message.includes('Invalid PDF')) {
        errorMessage = 'Arquivo PDF corrompido ou invlido'
      }

      const enhancedError = new Error(errorMessage)
      enhancedError.originalError = error
      enhancedError.pdfUrl = pdfUrl
      enhancedError.pageNumber = pageNumber

      throw enhancedError
    }
  }

  /**
   * Obter informaes do PDF (nmero de pginas, etc.)
   * @param {string} pdfUrl - URL do PDF
   * @returns {Promise<Object>} - Informaes do PDF
   */
  async getPDFInfo(pdfUrl) {
    try {
      const pdfjs = await loadPDFJS()
      const loadingTask = pdfjs.getDocument(pdfUrl)
      const pdf = await loadingTask.promise

      return {
        numPages: pdf.numPages,
        title: await this.getPDFMetadata(pdf, 'Title'),
        author: await this.getPDFMetadata(pdf, 'Author'),
        subject: await this.getPDFMetadata(pdf, 'Subject')
      }
    } catch (error) {
      console.error(' Erro ao obter informaes do PDF:', error)
      throw error
    }
  }

  /**
   * Obter metadados do PDF
   * @param {Object} pdf - Documento PDF
   * @param {string} key - Chave do metadado
   * @returns {Promise<string>} - Valor do metadado
   */
  async getPDFMetadata(pdf, key) {
    try {
      const metadata = await pdf.getMetadata()
      return metadata.info[key] || ''
    } catch (error) {
      return ''
    }
  }

  /**
   * Renderizar mltiplas pginas de um PDF
   * @param {string} pdfUrl - URL do PDF
   * @param {Array<number>} pageNumbers - Array com nmeros das pginas
   * @param {number} scale - Escala de renderizao
   * @returns {Promise<Array<string>>} - Array com imagens em base64
   */
  async renderMultiplePages(pdfUrl, pageNumbers, scale = 1.5) {
    const results = []

    for (const pageNumber of pageNumbers) {
      try {
        const imageData = await this.renderPageAsImage(pdfUrl, pageNumber, scale)
        results.push(imageData)
      } catch (error) {
        console.error(` Erro ao renderizar pgina ${pageNumber}:`, error)
        results.push(null)
      }
    }

    return results
  }
}

export default new PDFRenderService()